<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseTextarea from "@/components/common/Base.Textarea.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseSelect from "@/components/common/Base.Select.vue";
import { getNumber } from "@/helpers/getNumber";

export default {
  components: {
    BaseInput,
    BaseTextarea,
    BaseButton,
    BaseSelect,
  },
  computed: {
    price() {
      return (p) => {
        return getNumber(p.slice(0, -4));
      };
    },
    quantity() {
      return (q) => {
        return getNumber(q);
      };
    },
    totalDiscountedPrice() {
      let price = 0;
      console.log();
      this.products.forEach(
        (p) =>
          (price +=
            (getNumber(p.price.slice(0, -4)) * getNumber(p.quantity) * 30) /
            100)
      );
      return price;
    },
    totalPrice() {
      let price = 0;
      this.products.forEach(
        (p) =>
          (price += getNumber(p.price.slice(0, -4)) * getNumber(p.quantity))
      );
      return price;
    },
  },
  data() {
    return {
      conterparty: "",
      inn: "",
      address: "",
      phone: "",
      email: "",
      contact: "",
      validy: "",
      endCustomer: "",
      region: "",
      type: "",
      tzType: "",
      ready: "",
      productsSearch: "",
      companies: [
        {
          key: "asdad",
          value: "Aasdads",
        },
        {
          key: "asdasffefsfd",
          value: "Fefef",
        },
        {
          key: "dwede",
          value: "Gewfwef",
        },
      ],
      products: [
        {
          id: 1,
          article: "EP.01",
          name: "Помпа ирригационная эндоскопическая ENDOMATE PUMP",
          price: "268,900,00 ₽",
          quantity: "1 шт",
          discount: "30%",
        },
        {
          id: 2,
          article: "EM.CO2.01",
          name: "Эндоскопический инсуффлятор углекислого газа ENDOMATE C02",
          price: "542,750,00 ₽",
          quantity: "1 шт",
          discount: "30%",
        },
      ],
      company: "",
      suggestions: ["8/21-ES", "7/21-ES", "6/21-ES", "5/21-ES"],
    };
  },
  methods: {
    addProduct() {
      console.log("addProduct");
    },
    removeProduct(id) {
      let index = this.products.findIndex((p) => p.id == id);
      this.products.splice(index, 1);
    },
  },
};
</script>

